.photo {
  display: inline-block;
  float: left;
  width: 50px;
  height: 50px;
  opacity: 0.4;
}

.message {
  display: table-cell;
  width: 150px;
  height: 50px;
  text-align: left;
  vertical-align: middle;
  font-family: sans-serif;
  font-size: 12px;
}

.delete {
  display: inline-block;
  width: 18px;
  height: 18px;
  float: right;
  vertical-align: bottom;
  cursor: pointer;
  opacity: 0.7;
}

.comment {
  padding: 4px;
  height: 68px;
  width: 300px;
  border: 1px solid ligthgray;
  vertical-align: middle;
  text-align: center;
  display: table;
}

img {
  width: 100%;
  height: 100%;
}

.commentbox {
  margin-bottom: 6px;
}

.input {
  width: 230px;
  display: inline-block;
  height: 24px;
  padding: 2px;
  margin-right: 4px;
  font-size: 14px;
}

.button {
  display: inline-block;
  width: 50px;
  text-align: center;
  border: solid 1px dodgerblue;
  color: black;
  cursor: pointer;
  line-height: 26px;
  font-size: 14px;
  background-color: lightsteelblue;
}

